import {
	useBlockProps,
	MediaUpload,
	MediaUploadCheck,
} from "@wordpress/block-editor";
import { Button, TextControl } from "@wordpress/components";
import { Fragment } from "@wordpress/element";

export default function Edit({ attributes, setAttributes }) {
	const { slides = [] } = attributes;

	const updateSlide = (index, key, value) => {
		const newSlides = [...slides];
		newSlides[index][key] = value;
		setAttributes({ slides: newSlides });
	};

	const addSlide = () => {
		const newSlides = [
			...slides,
			{ title: "New Slide", imageUrl: "", imageId: null },
		];
		setAttributes({ slides: newSlides });
	};

	const removeSlide = (index) => {
		const newSlides = [...slides];
		newSlides.splice(index, 1);
		setAttributes({ slides: newSlides });
	};

	const moveSlide = (from, to) => {
		const newSlides = [...slides];
		const [moved] = newSlides.splice(from, 1);
		newSlides.splice(to, 0, moved);
		setAttributes({ slides: newSlides });
	};

	return (
		<div {...useBlockProps()}>
			<h3>Swiper Slider Block</h3>
			{slides.map((slide, index) => (
				<div
					key={index}
					style={{
						border: "1px solid #ccc",
						marginBottom: "10px",
						padding: "10px",
					}}
				>
					<TextControl
						label={`Slide ${index + 1} Title`}
						value={slide.title}
						onChange={(val) => updateSlide(index, "title", val)}
					/>

					<MediaUploadCheck>
						<MediaUpload
							onSelect={(media) => updateSlide(index, "imageUrl", media.url)}
							allowedTypes={["image"]}
							render={({ open }) => (
								<div>
									<Button onClick={open} variant="primary">
										{slide.imageUrl ? "Change Image" : "Upload Image"}
									</Button>
									{slide.imageUrl && (
										<div>
											<img
												src={slide.imageUrl}
												alt=""
												style={{ maxWidth: "100%", marginTop: "10px" }}
											/>
											<Button
												onClick={() => updateSlide(index, "imageUrl", "")}
												variant="secondary"
												isDestructive
											>
												Remove Image
											</Button>
										</div>
									)}
								</div>
							)}
						/>
					</MediaUploadCheck>

					<div style={{ marginTop: "10px" }}>
						<Button
							onClick={() => removeSlide(index)}
							isDestructive
							variant="secondary"
						>
							Remove Slide
						</Button>{" "}
						{index > 0 && (
							<Button onClick={() => moveSlide(index, index - 1)}>
								↑ Move Up
							</Button>
						)}{" "}
						{index < slides.length - 1 && (
							<Button onClick={() => moveSlide(index, index + 1)}>
								↓ Move Down
							</Button>
						)}
					</div>
				</div>
			))}
			<Button
				onClick={addSlide}
				variant="primary"
				style={{ marginTop: "10px" }}
			>
				+ Add Slide
			</Button>
		</div>
	);
}
